/* Desktop */
.header-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: end;
      justify-content: flex-end;
  padding: 2em 5.938em;
  -webkit-transition: ease all .75s;
  -o-transition: ease all .75s;
  transition: ease all .75s; }

.headeritem-container {
  margin: 0 1em;
  font-size: 1.063em;
  -webkit-transition: ease all .75s;
  -o-transition: ease all .75s;
  transition: ease all .75s; }

.headeritem-text {
  text-decoration: initial;
  color: initial; }
  .headeritem-text:hover {
    opacity: .5;
    cursor: default; }
    .headeritem-text:hover:not(#ddp) {
      cursor: pointer; }

.headeritem-dropdown-text {
  padding: 0.5em 1em; }
  .headeritem-dropdown-text:hover {
    color: rgba(0, 0, 0, 0.5); }

.headeritem-border {
  border-top: 1px solid #DCDCDC; }

.headeritem-icon {
  padding-left: 0.375em;
  width: 0.75em;
  height: 0.75em; }

.headeritem-dropdown {
  padding: .25em 0 !important;
  width: 10.938em !important;
  font-size: 1.063em !important;
  pointer-events: auto !important;
  background-color: white !important;
  -webkit-box-shadow: 0 50px 100px rgba(50, 50, 93, 0.1), 0 15px 35px rgba(50, 50, 93, 0.15), 0 5px 15px rgba(0, 0, 0, 0.1) !important;
          box-shadow: 0 50px 100px rgba(50, 50, 93, 0.1), 0 15px 35px rgba(50, 50, 93, 0.15), 0 5px 15px rgba(0, 0, 0, 0.1) !important;
  opacity: 1 !important; }
  .headeritem-dropdown:after {
    border-bottom-color: white !important; }
  .headeritem-dropdown:hover {
    visibility: visible !important; }
  .headeritem-dropdown:hover + #ddp {
    opacity: .5; }

/* Tablet */
@media screen and (min-width: 481px) and (max-width: 1025px) {
  .header-container {
    padding: 2em 1.250em; }
  .headeritem-container {
    margin: 0 .625em;
    font-size: 1em; } }

/* Mobile */
@media screen and (max-width: 480px) {
  .header-container {
    padding: 2em 0.75em; }
  .headeritem-container {
    margin: 0 .5em;
    font-size: 0.875em; } }



