/* Desktop */
.footer-container {
  padding: 0 2em;
  min-height: 4em;
  background-color: #5b605d;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -webkit-transition: left .75s;
  -o-transition: left .75s;
  transition: left .75s; }

.footer-item {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  padding: 0 1em;
  text-align: center;
  font-size: .875em;
  -webkit-transition: ease all .75s;
  -o-transition: ease all .75s;
  transition: ease all .75s; }

.footer-icons {
  cursor: pointer;
  padding: 0 .375em;
  width: 1.714em;
  height: 1.714em;
  -webkit-transition: ease all .75s;
  -o-transition: ease all .75s;
  transition: ease all .75s; }

/* Tablet */
