    padding-bottom: 1em; } }

/* Mobile */
@media screen and (max-width: 480px) {
  .footer-container {
    padding: 0 .875em;
    min-height: 12.688em;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: start;
        justify-content: flex-start; }
  .footer-item {
    padding: .438em 0; }
  .footer-item:first-child {
    padding-top: 1.438em; }
  .footer-item:last-child {
    padding-top: 1em; }
  .footer-icons {
    cursor: pointer;
    padding: 0 .375em;
    width: 2.125em;
    height: 2.125em; } }



