/* Desktop */
/* index.js */
.not-found-container {
  padding: 0 5.938em;
  min-height: calc(100vh - 5.875em - 4em); }
  .not-found-container > * {
    text-align: center; }
  .not-found-container > :first-child {
    font-size: 7.563em;
    color: #5b605d; }
  .not-found-container > :nth-child(2) {
    font-size: 2.313em; }

/* Tablet */
@media screen and (min-width: 481px) and (max-width: 1025px) {
  .not-found-container {
    padding: 0 1.250em;
    min-height: calc(100vh - 5.813em - 5em); }
    .not-found-container > :nth-child(2) {
      font-size: 1.813em; } }

/* Mobile */
@media screen and (max-width: 480px) {
  .not-found-container {
    padding: 0 0.875em;
    min-height: calc(100vh - 5.563em - 12.688em); }
    .not-found-container > :first-child {
      font-size: 2.313em; }
    .not-found-container > :nth-child(2) {
      font-size: .875em; } }



