/* Desktop */
/* index.js */
.temporary-container {
  padding: 0 5.938em;
  min-height: calc(100vh - 5.875em - 4em);
  line-height: calc(100vh - 5.875em * 2 - 4em);
  text-align: center; }
  .temporary-container > :first-child {
    font-size: 3.5em; }

/* Tablet */
@media screen and (min-width: 481px) and (max-width: 1025px) {
  .temporary-container {
    padding: 0 1.250em;
    min-height: calc(100vh - 5.813em - 5em);
    text-align: center; } }

/* Mobile */
@media screen and (max-width: 480px) {
  .temporary-container {
    padding: 0 0.875em;
    min-height: calc(100vh - 5.563em - 12.688em);
    line-height: calc(100vh - 5.563em * 2 - 12.688em);
    text-align: center; }
    .temporary-container > :first-child {
      font-size: 2.313em; } }



